body {
  margin: 0;
  padding: 0;
  font-family: sans-serif;
  overflow: hidden;
}

#touchPane {
  display: none;
}

.touchPaneLeft {
    position: fixed;
    left: 0;
    width: 50%;
    height: 100%;
}

.touchPaneRight {
    position: fixed;
    left: 50%;
    width: 50%;
    height: 100%;
}

.guiPane, .dg.main {
    position: absolute;
    right: 10px;
}

.touchButton {
  display: none;
  position: absolute;
  width: 100px;
  height: 100px;
  margin-left: -50px;
  margin-top: -50px;
  background: white;
  opacity: 0.3;
  -webkit-transition: opacity 250ms;
  -o-transition: opacity 250ms;
  transition: opacity 250ms;
  border-radius: 50%;
}

.hud-target {
  position: fixed;
  left: 0;
  top: 0;
  width: 20px;
  height: 20px;
  border: 1px solid #0f0;
  border-radius: 20px;
}

.hud #horizon {
  width: 300px;
  position: fixed;
  left: 50%;
  top: 50%;
  border: 0.5px solid #0f0;
  -webkit-transform: translateX(-50%);
      -ms-transform: translateX(-50%);
          transform: translateX(-50%);
  opacity: 0.8;
}

.hud #target {
  position: fixed;
  -webkit-transform: translateX(-16px) translateY(-16px);
      -ms-transform: translateX(-16px) translateY(-16px);
          transform: translateX(-16px) translateY(-16px);
  height: 32px;
  opacity: .8;
}
.hud #target:before, .hud #target:after {
  position: absolute;
  left: 15px;
  content: ' ';
  height: 33px;
  width: 2px;
  background-color: #0f0;
}
.hud #target:before {
  -webkit-transform: rotate(45deg);
      -ms-transform: rotate(45deg);
          transform: rotate(45deg);
}
.hud #target:after {
  -webkit-transform: rotate(-45deg);
      -ms-transform: rotate(-45deg);
          transform: rotate(-45deg);
}

#focal {
  position: fixed;
  left: 50%;
  top: 50%;
  -webkit-transform: translateX(-50%) translateY(-50%);
      -ms-transform: translateX(-50%) translateY(-50%);
          transform: translateX(-50%) translateY(-50%);
  height: 150px;
  width: 150px;
  border-radius: 150px;
  border: 2px solid #0f0;
  opacity: 0.8;
}



